











































































import { Component, Vue, Prop } from "vue-property-decorator";
import { Domain, Server } from "../data/index";
import api from "../data/apiConfig";
import env from "../data/environment";

var apiUrl = api.url(env);

@Component({})
export default class List extends Vue {
  @Prop({ default: false }) private loading!: boolean;
  @Prop({ default: false }) private showData!: boolean;
  @Prop({ default: [] }) domains!: Domain[];
  data() {
    return {
      menu: [
        {
          header: true,
          title: "Main Navigation"
        },
        {
          href: "/",
          title: "Check Domain",
          icon: "fas fa-search"
        },
        {
          href: "/list",
          title: "List Checked Domains",
          icon: "fas fa-list"
        }
      ]
    };
  }
  getList() {
    this.loading = true;
    this.showData = false;

    fetch(apiUrl + "domain/list", {
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      }
    })
      .then(response => response.json())
      .then(data => {
        this.domains = data;
        this.loading = false;
        this.showData = true;
      });
  }
  beforeMount() {
    this.getList();
  }
}
